
'use client';

import { useEffect, useState } from 'react';
import { database } from '@/lib/firebase';
import { ref, onValue, off } from 'firebase/database';
import { Header } from '@/components/Header';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';

// A generic interface to represent a meter device from Firebase
interface FirebaseDevice {
    key: string;
    Address: string;
    "Contact Number": string;
    Email: string;
    "Magnetic Contactor Mode": string;
    Name: string;
    Outage: string;
    "Paid Status": boolean;
    Price: number;
    kwhr: number;
    status: string;
    tampering: string;
}


export default function DashboardPage() {
  const [meterData, setMeterData] = useState<FirebaseDevice[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const metersRef = ref(database, 'devices');
    const listener = onValue(metersRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const dataArray: FirebaseDevice[] = Object.keys(data).map(key => ({
            ...data[key],
            key: key
        }));
        setMeterData(dataArray);
        setError(null);
      } else {
        setError("No data available in the database path '/devices'.");
      }
      setLoading(false);
    }, (error) => {
      console.error(error);
      setError("Failed to fetch data. Check console for details.");
      setLoading(false);
    });

    // Detach the listener when the component unmounts
    return () => {
      off(metersRef, 'value', listener);
    };
  }, []);

  return (
    <div className="flex min-h-screen w-full flex-col">
      <Header />
      <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
        <div className="flex items-center">
          <h1 className="font-headline text-2xl font-bold md:text-3xl">
            Firebase Realtime Data
          </h1>
        </div>
        <Card>
          <CardHeader>
            <CardTitle>Live Data from /devices</CardTitle>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="space-y-2">
                <Skeleton className="h-12 w-full" />
                <Skeleton className="h-12 w-full" />
                <Skeleton className="h-12 w-full" />
              </div>
            ) : error ? (
              <p className="text-destructive">{error}</p>
            ) : (
                <div className="overflow-x-auto">
                    <Table>
                        <TableHeader>
                        <TableRow>
                            <TableHead>Device ID</TableHead>
                            <TableHead>Name</TableHead>
                            <TableHead>Address</TableHead>
                            <TableHead>Contact Number</TableHead>
                            <TableHead>Email</TableHead>
                            <TableHead>kWh</TableHead>
                            <TableHead>Price</TableHead>
                            <TableHead>Paid Status</TableHead>
                            <TableHead>Status</TableHead>
                            <TableHead>Outage</TableHead>
                            <TableHead>Tampering</TableHead>
                            <TableHead>Contactor Mode</TableHead>
                        </TableRow>
                        </TableHeader>
                        <TableBody>
                        {meterData.map((device) => (
                            <TableRow key={device.key}>
                                <TableCell className="font-mono">{device.key}</TableCell>
                                <TableCell>{device.Name}</TableCell>
                                <TableCell>{device.Address}</TableCell>
                                <TableCell>{device["Contact Number"]}</TableCell>
                                <TableCell>{device.Email}</TableCell>
                                <TableCell>{device.kwhr}</TableCell>
                                <TableCell>{device.Price}</TableCell>
                                <TableCell>{String(device["Paid Status"])}</TableCell>
                                <TableCell>{device.status}</TableCell>
                                <TableCell>{device.Outage}</TableCell>
                                <TableCell>{device.tampering}</TableCell>
                                <TableCell>{device["Magnetic Contactor Mode"]}</TableCell>
                            </TableRow>
                        ))}
                        </TableBody>
                    </Table>
                </div>
            )}
          </CardContent>
        </Card>
      </main>
    </div>
  );
}
